import {useRef, useEffect} from 'react';
import {__} from '@wordpress/i18n';
import {registerPaymentMethod} from '@woocommerce/blocks-registry';
import {getSetting} from '@woocommerce/settings';
import {decodeEntities} from '@wordpress/html-entities';
import {loadStripe, createSetupIntent, parsePaymentData} from '../stripe';
import {type PaymentMethodInterface} from '@woocommerce/types';
import {startTransaction, updateTransaction, z} from '@peachpay/checkout-blocks';
import {type CheckoutAfterProcessingWithErrorEventData} from '@woocommerce/checkout';
import CurrencyFallback from '../../../../../blocks/src/currency-fallback';
import {getErrorMessage, getErrorString} from '../../../../../../frontend/@shared/ts/error';

const SettingsSchema = z.object({
	title: z.string(),
	icon_url: z.string(),
	conditions: z.string(),
	supports: z.string().array(),
	connect_id: z.string(),
	public_key: z.string(),
	setup_intent_url: z.string(),
	setup_intent_nonce: z.string(),
	currency_fallback_required: z.string().nullable(),
});

const parsedResult = SettingsSchema.safeParse(
	getSetting('peachpay_stripe_sepadebit_data', false),
);

if (!parsedResult.success) {
	throw new Error(`PeachPay Stripe SEPA Direct Debit data is not available.\n\n${parsedResult.error.message}`);
}

const settings = parsedResult.data;
const title = decodeEntities(settings.title);

loadStripe(settings.public_key, settings.connect_id)
	.then(stripe => {
		if (stripe === null) {
			throw new Error('Stripe is null');
		}

		if (typeof stripe === 'string') {
			throw new Error(stripe);
		}

		const options = {
			style: {
				base: {
					color: '#32325d',
					fontSize: '16px',
				},
			},

			supportedCountries: ['SEPA'],
		};

		const ibanElement = stripe.elements().create('iban', options);

		/**
		 * Content component
		 */
		const Content = (context: PaymentMethodInterface) => {
			const {emitResponse, eventRegistration, billing} = context;
			const {onPaymentSetup, onCheckoutSuccess, onCheckoutFail} = eventRegistration;

			const containerRef = useRef<HTMLDivElement | null>(null);

			useEffect(() => {
				if (containerRef.current) {
					containerRef.current.innerHTML = '';
					ibanElement.mount(containerRef.current);
				}
			}, []);

			useEffect(() => {
				const unsubscribe = onPaymentSetup(async () => {
					const {result: transactionId, error: transactionError} = await startTransaction('peachpay_stripe_sepadebit');

					if (transactionError) {
						return {
							type: emitResponse.responseTypes.ERROR,
							message: getErrorMessage(transactionError),
						};
					}

					if (transactionId === undefined) {
						return {
							type: emitResponse.responseTypes.ERROR,
							message: __('Missing transaction ID', 'peachpay-for-woocommerce'),
						};
					}

					try {
						const {paymentMethod, error} = await stripe.createPaymentMethod({
							type: 'sepa_debit',
							sepa_debit: ibanElement,
							billing_details: {
								address: {
									city: billing.billingAddress.city,
									country: billing.billingAddress.country,
									line1: billing.billingAddress.address_1,
									line2: billing.billingAddress.address_2,
									postal_code: billing.billingAddress.postcode,
									state: billing.billingAddress.state,
								},
								email: billing.billingAddress.email,
								name: billing.billingAddress.first_name + ' ' + billing.billingAddress.last_name,
								phone: billing.billingAddress.phone,
							},
						});

						if (error) {
							const note = error.message ?? __('Failed to tokenize payment method.', 'peachpay-for-woocommerce');
							await updateTransaction(transactionId, {note});
							return {
								type: emitResponse.responseTypes.ERROR,
								message: note,
							};
						}

						if (!paymentMethod.id) {
							const note = __('Failed to tokenize payment method, ID missing.', 'peachpay-for-woocommerce');
							await updateTransaction(transactionId, {note});
							return {
								type: emitResponse.responseTypes.ERROR,
								message: note,
							};
						}

						if (billing.cartTotal.value > 0) {
							return {
								type: emitResponse.responseTypes.SUCCESS,
								meta: {
									paymentMethodData: {
										peachpay_transaction_id: transactionId,
										peachpay_stripe_payment_method_id: paymentMethod.id,
									},
								},
							};
						}

						const setupIntent = await createSetupIntent({
							payment_method_type: 'sepa_debit',
							payment_method_ID: paymentMethod.id,
							setup_intent_url: settings.setup_intent_url,
							setup_intent_nonce: settings.setup_intent_nonce,
						});

						if (setupIntent === null) {
							const note = __('Failed to create setup intent.', 'peachpay-for-woocommerce');
							await updateTransaction(transactionId, {note});
							return {
								type: emitResponse.responseTypes.ERROR,
								message: note,
							};
						}

						const {error: confirmError, setupIntent: confirmIntent} = await stripe.confirmSepaDebitSetup(
							setupIntent.setup_intent_details.client_secret,
							{
								payment_method: {
									sepa_debit: ibanElement,
									billing_details: {
										name: billing.billingAddress.first_name + ' ' + billing.billingAddress.last_name,
										email: billing.billingAddress.email,
									},
								},
							},
						);

						if (confirmError) {
							const note = confirmError.message ?? __('Failed to confirm setup intent.', 'peachpay-for-woocommerce');
							await updateTransaction(transactionId, {note});
							return {
								type: emitResponse.responseTypes.ERROR,
								message: note,
							};
						}

						if (confirmIntent.status !== 'succeeded') {
							const note = `${__('Setup intent resulted in invalid status', 'peachpay-for-woocommerce')} (${confirmIntent.status})`;
							await updateTransaction(transactionId, {note});
							return {
								type: emitResponse.responseTypes.ERROR,
								message: note,
							};
						}

						if (
							typeof confirmIntent.payment_method === 'object' || confirmIntent.payment_method === null
						) {
							const note = `${__('Payment method invalid type', 'peachpay-for-woocommerce')} (${typeof confirmIntent.payment_method})`;
							await updateTransaction(transactionId, {note});
							return {
								type: emitResponse.responseTypes.ERROR,
								message: note,
							};
						}

						return {
							type: emitResponse.responseTypes.SUCCESS,
							meta: {
								paymentMethodData: {
									peachpay_transaction_id: transactionId,
									peachpay_stripe_payment_method_id: confirmIntent.payment_method,
								},
							},
						};
					} catch (error) {
						const note = getErrorString(error);
						await updateTransaction(transactionId, {note});
						return {
							type: emitResponse.responseTypes.ERROR,
							message: note,
						};
					}
				});

				return unsubscribe;
			}, [onPaymentSetup, billing.billingAddress, billing.cartTotal]);

			useEffect(() => {
				const unsubscribe = onCheckoutSuccess(async (data: {redirectUrl: string}) => {
					const dataURL = new URL(data.redirectUrl);

					const [name, encodedStringData] = dataURL.hash.split('=');

					if (dataURL.hash === '' || name !== '#payment_data' || !encodedStringData) {
						return;
					}

					const paymentData = parsePaymentData(encodedStringData);

					if (paymentData === null) {
						return {
							type: emitResponse.responseTypes.ERROR,
							message: __('Failed to parse payment data', 'peachpay-for-woocommerce'),
						};
					}

					const clientSecret = paymentData.data.client_secret;
					const successURL = paymentData.success_url;

					const {paymentIntent, error} = await stripe.retrievePaymentIntent(clientSecret);
					if (!paymentIntent || error) {
						return {
							type: emitResponse.responseTypes.ERROR,
							message: error?.message ?? __('Failed to retrieve payment intent.', 'peachpay-for-woocommerce'),
							messageContext: emitResponse.noticeContexts.PAYMENTS,
						};
					}

					if (paymentIntent.status === 'succeeded' || paymentIntent.status === 'requires_capture' || paymentIntent.status === 'processing') {
						return {
							type: emitResponse.responseTypes.SUCCESS,
							redirectUrl: successURL,
						};
					}

					if (paymentIntent.status === 'requires_action') {
						const {paymentIntent, error} = await stripe.confirmSepaDebitPayment(clientSecret);
						if (!paymentIntent || error) {
							return {
								type: emitResponse.responseTypes.ERROR,
								message: error?.message ?? __('Failed to confirm payment intent.', 'peachpay-for-woocommerce'),
								messageContext: emitResponse.noticeContexts.PAYMENTS,
							};
						}

						if (paymentIntent.status === 'succeeded' || paymentIntent.status === 'requires_capture' || paymentIntent.status === 'processing') {
							return {
								type: emitResponse.responseTypes.SUCCESS,
								redirectUrl: successURL,
							};
						}
					}

					return {
						type: emitResponse.responseTypes.ERROR,
						message: __('Something went wrong, but the payment may have gone through. Please contact store support.', 'peachpay-for-woocommerce'),
						messageContext: emitResponse.noticeContexts.PAYMENTS,
					};
				});

				return unsubscribe;
			}, [onCheckoutSuccess]);

			useEffect(() => {
				const unsubscribe = onCheckoutFail((data: CheckoutAfterProcessingWithErrorEventData) => {
					if (data.processingResponse?.paymentStatus === emitResponse.responseTypes.FAIL) {
						return {
							type: emitResponse.responseTypes.FAIL,
							message: data.processingResponse.paymentDetails['message'],
							messageContext: emitResponse.noticeContexts.PAYMENTS,
						};
					}

					if (data.processingResponse?.paymentStatus === 'not set' && data.processingResponse.message) {
						return {
							type: 'not set',
							message: data.processingResponse.message,
							messageContext: emitResponse.noticeContexts.CHECKOUT,
						};
					}

					return {
						type: emitResponse.responseTypes.ERROR,
						message: __('Something went wrong while processing the request.', 'peachpay-for-woocommerce'),
						messageContext: emitResponse.noticeContexts.PAYMENTS,
					};
				});

				return unsubscribe;
			}, [onCheckoutFail]);

			if (settings.currency_fallback_required) {
				return <CurrencyFallback name={title} currency={settings.currency_fallback_required} />;
			}

			return (
				<div>
					<div ref={containerRef}>Loading...</div>
					<p style={{fontSize: 'smaller', textAlign: 'justify', paddingTop: '16px'}}>
						{settings.conditions}
					</p>
				</div>
			);
		};

		const Label = (props: PaymentMethodInterface) => {
			const {PaymentMethodLabel, PaymentMethodIcons} = props.components;
			return (
				<>
					<PaymentMethodLabel text={title} />
					<PaymentMethodIcons icons={[{
						id: 'peachpay_stripe_sepadebit',
						src: settings.icon_url,
						alt: title,
					}]} className='pp-label-icon' />
				</>
			);
		};

		registerPaymentMethod({
			ariaLabel: title,
			canMakePayment(data) {
				return data.paymentMethods.includes('peachpay_stripe_sepadebit');
			},
			// @ts-expect-error Missing types are expected because WooCommerce gives the props
			content: <Content />,
			// @ts-expect-error Missing types are expected because WooCommerce gives the props
			edit: <Content />,
			// @ts-expect-error Missing types are expected because WooCommerce gives the props
			label: <Label />,
			name: 'peachpay_stripe_sepadebit',
			supports: {
				features: settings.supports,
			},
		});
	})
	.catch(error => {
		throw error as Error;
	});
